/*  MEDIA STYLES WILL BE HERE  */
@media (max-width: 1399px) {
    .header__contacts {
        display: none;
    }

    .header__container {
        justify-content: space-around;
    }
}

@media (max-width: 1280px) {
    .header__container {
        justify-content: space-between;
    }
}

@media (max-width: 1280px) {
    .header__container {
        justify-content: space-between;
    }
}

@media (max-width: 1200px) {
    .header__container {
        justify-content: space-around;
    }

    .header__nav-list {
        column-gap: 2.5em;
    }

    .mission__subtitle {
        max-width: 75% !important;
    }
}

@media (max-width: 1024px) {
    .burger-button {
        display: block;
    }

    .header__nav-list {
        display: none;
    }

    .header__container {
        justify-content: space-between;
    }

    .page {
        font-size: 14px;
    }

    .prev,
    .next {
        margin-top: 0.3em !important;
    }
}

@media (max-width: 911px) {
    .motivation__picture {
        padding: 0 !important;
    }

    .motivation__container {
        min-height: calc(92vh - 104px) !important;
    }

    .motivation__desc {
        text-align: center;
        row-gap: 3em !important;
    }

}

@media (max-width: 768px) {
    .motivation__container {
        flex-wrap: wrap;
    }

    .motivation__desc {
        row-gap: 2em !important;
        order: 1;
    }

    .path__title,
    .mission__title,
    .coaches__title,
    .gallery__title {
        padding: 2em 0 .5em 0 !important;
    }

    .mission,
    .path,
    .coaches {
        padding: 0 !important;
    }

    .path__list {
        flex-wrap: wrap;
        row-gap: 2em;
    }

    .gallery {
        padding: 0 0 70px 0 !important;
    }

.footer__container {
    justify-content: space-around;
    column-gap: 12px !important;
    row-gap: 2em;
    text-align: center;
}

}

@media (max-width: 542px) {
    .footer__nav {
        order: 0;
    }

    .footer__contacts {
        order: 1;
    }

    .footer__contacts-list {
        max-height: 130px;
    }

    .footer__nav,
    .footer__contacts,
    .footer__worktime,
    .footer__address {
        flex: 0 0 45%;
        text-align: center;
    }

}

@media (max-width: 500px) {
    .page {
        font-size: 13px;
    }

    .motivation__desc-title {
        font-size: 4.5rem !important;
    }

}